.JoinPage {
    background-color: rgb(31, 29, 29);
    width: 100%;
    height: 100vh;
    padding: 0;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.JoinContainer {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.JoinContainer>img {
    width: 10vmax;
}

.JoinContainer>h1 {
    color: white;
    font-size: 2.5vmax;
    width: 25vmax;
    border-bottom: 0.2vmax solid white;
    text-align: center;
    padding: 1vmax;
    font-family: "poppins";
    box-sizing: border-box;
}

#joinInput {
    width: 25vmax;
    padding: 1.5vmax;
    border: none;
    outline: none;
    font-size: 1.2vmax;
    margin: 2vmax;
    box-sizing: border-box;
}

.joinbtn {
    width: 25vmax;
    padding: 1.5vmax;
    border: none;
    color: white;
    font-family: "poppins";
    outline: none;
    font-size: 1.2vmax;
    background-color: #2BBEF7;
    box-sizing: border-box;
    transition: all 0.3s;
    cursor: pointer;
}

.joinbtn:hover {
    background-color: rgb(111, 194, 151);
}