.chatPage {
    background-color: rgb(31, 29, 29);
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.chatContainer {
    background-color: white;
    height: 60%;
    width: 50%;
}

.header {
    background-color: #2BBEF7;
    height: 15%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header>h2 {
    font-size: 1.5vmax;
    color: white;
    margin: 2.5vmax;
}

.header img {
    margin: 2.5vmax;
    transition: all 0.3s;
    cursor: pointer;
    width: 4vh;
    height: 4vh;
}

.header>img:hover {
    transform: scale(1.2);
}

.chatBox {
    height: 70%;
    box-sizing: border-box;
}

.inputBox {
    height: 15%;
    box-sizing: border-box;
    display: flex;
}

#chatInput {
    width: 80%;
    border: none;
    padding: 2vmax;
    box-sizing: border-box;
    background-color: white;
    outline: none;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 1.2vmax;
    border-top: 0.2vmax solid rgba(41, 41, 41, 0.562);
}

.sendBtn {
    background-color: #2BBEF7;
    color: white;
    border: none;
    width: 21%;
    transition: all 0.3s;
    cursor: pointer;
}

.sendBtn>img {
    width: 2.3vmax;
    filter: brightness(100) invert();
    transition: all 0.3s;
}

.sendBtn:hover {
    background-color: rgb(111, 194, 151);
}

.sendBtn:hover img {
    transform: translateX(6px);
}

@media screen and (min-width: 0em) and (max-width:994px) {
    .header img {
        margin: 2.5vmax;
        transition: all 0.3s;
        cursor: pointer;
        width: 2vh;
        height: 2vh;
    }
}